@use '../base';

.testimonials {
	text-align: center;
	padding: 4rem 1.5rem 3.5rem;
    background-color: #FFFCF8;
}

.testimonials__img {
	border-radius: 50%;
	margin: auto;
    max-width: 4.5rem;
}

.testimonials__header {
	color: #a7aaad;
	font-size: 1rem;
	letter-spacing: 4px;
	text-transform: uppercase;
}

.testimonial__list {
	margin-top: 4.5rem;
}

.testimonial__content {
	margin-top: 2rem;
}

.testimonial__content__item {
	line-height: 1.9;
    padding-bottom: 2rem;
}

.testimonial__content__heading {
	display: flex;
	flex-direction: column;
    gap: 1rem;

    span {
        font-family: base.$main-font;
        font-size: 0.875rem;
        color: #a7aaad;
    }
}

.testimonials__item {
	max-width: 30rem;
	margin: auto auto 4.5rem;
	//margin-bottom: 4.5rem;
}

@media screen and (min-width: 620px) { 
	.testimonial__list {
		display: flex;
		flex-direction: row;
		gap: 1rem;
	}
}

